import React, { Component } from 'react'
import style from './login.module.less'
import logo from '../../assets/images/logo.png'
import { Form, Icon, Input, Button,message} from 'antd'
import {reqLogin} from '../../api/index'
import memoryUtils from '../../utils/memoryUtils'
import storageUtils from '../../utils/storageUtils'
import {Redirect} from 'react-router-dom'
class Login extends Component {
    handleSubmit= (e)=>{
        //
        e.preventDefault()
        // 
        this.props.form.validateFields(async (err, values) => {
          if (!err) {
            let {username,password} = values
             let result = await reqLogin(username,password)
              if(result.status === 0){
                message.success('')
                // 
                memoryUtils.user = result.data
                // 
                storageUtils.saveUser(result.data)
                // 
                this.props.history.replace('/')

              }else{
                message.error(result.msg)
              }
          }
        });
    }
render() {
    if(memoryUtils.user._id){
      return <Redirect to='/'></Redirect>
    }
    const { getFieldDecorator } = this.props.form;
    return (
    <div className={style.login}>
        <header className={style.loginHeader}>
            <img src={logo} alt="" />
            <h1 style={{margin:0}}>React</h1>
        </header>
        <section className={style.loginContent}>
            <h2>
        <Icon type="twitter" style={{ 'margin-right': '15px' }}/>
            </h2>
        <Form onSubmit={this.handleSubmit} className="login-form">
            <Form.Item>
            {getFieldDecorator('username', {
            rules: [{ required: true,whitespace:true,message: '!' }],
                    initialValue: 'admin'
          })(
            <Input
              prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
              placeholder="Username"
            />,
          )}
            </Form.Item>
            <Form.Item>
            {getFieldDecorator('password', {
            rules: [{ required: true,whitespace:true,message: '!' },
                    { min: 4, message: '4!' },
                    { max: 12, message: '!' },
                    { pattern: /^[a-zA-Z0-9_]+$/, message: '!' },],
          })(
            <Input
              prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
              type="password"
              placeholder="Password"
            />,
          )}
            </Form.Item>
            <Form.Item>
            <Button type="primary" htmlType="submit" 
            className="login-form-button" style={{'width':"100%"}}>
                
                </Button>
            </Form.Item>
            </Form>
        </section>
    </div>
    )
}
}
//  Loginpropsform 
// form
const WrapLogin = Form.create()(Login)
export default WrapLogin
