import React, { Component } from 'react'
import { Card, Form, Input, Button, Icon, Cascader, message } from 'antd'
import { reqCategory, reqAddOrUpdateProduct } from '../../api'
import PicturesWall from '../../components/product-pictures-wall/pictures-wall'
import RichTextEditor from '../../components/rich-text-editor'

const { Item } = Form
const { TextArea } = Input
class ProductAddUpdate extends Component {
    constructor(props) {
        super(props)
        this.pw = React.createRef() // 
        this.editor = React.createRef()  //
    }
    state = {
        options: []
    }
    loadData = async selectedOptions => {
        // 
        const targetOption = selectedOptions[0]
        // loading
        targetOption.loading = true;
        // 
        const data = await this.getCategory(targetOption.value)
        // 
        if (data && data.length > 0) {
            // chidOption
            const childOption = data.map(item => ({
                value: item._id,
                label: item.name,
                isLeaf: true,
            }))
            targetOption.children = childOption
        } else {
            // 
            targetOption.isLeaf = true
        }
        // loading
        targetOption.loading = false;

        // 
        this.setState({
            options: [...this.state.options],
        })
    }
    // 0
    validatePrice = (rule, value, callback) => {
        if (value * 1 > 0) {
            callback()
        } else {
            callback('0')
        }
    }
    // 
    submit = () => {
        this.props.form.validateFields(async (err, values) => {
            if (!err) {
                // 
                let { name, desc, price, categoryIds } = values
                let pCategoryId;
                let categoryId
                if (categoryIds.length > 1) {
                    pCategoryId = categoryIds[0]
                    categoryId = categoryIds[1]
                } else {
                    pCategoryId = '0'
                    categoryId = categoryIds[0]
                }
                let _id = this.product._id
                //  imgs
                const imgs = this.pw.current.getImgs()
                // 
                const detail = this.editor.current.getDetail()
                // product
                let product = { name, desc, price, pCategoryId, categoryId, imgs, detail, _id }
                // 
                const result = await reqAddOrUpdateProduct(product)
                if (result.status === 0) {
                    message.success(`${this.isUpdate ? '' : ''}`)
                    this.props.history.go(-1)
                } else {
                    message.error(`${this.isUpdate ? '' : ''}`)
                }
            }
        })
    }
    // /
    getCategory = async (pCategoryId) => {
        const result = await reqCategory(pCategoryId)
        if (result.status === 0) {
            // 0 
            if (pCategoryId === '0') {
                this.initOption(result.data)
            } else {
                // 
                return result.data
            }
        }
    }
    // 
    initOption = async (categorys) => {
        // 
        const options = categorys.map(item => ({
            value: item._id,
            label: item.name,
            isLeaf: false,
        }))
        // 
        const { pCategoryId } = this.product
        const { isUpdate } = this
        if (isUpdate && pCategoryId !== '0') {
            const subCategorys = await this.getCategory(pCategoryId)
            // options
            const childOption = subCategorys.map(item => ({
                value: item._id,
                label: item.name,
                isLeaf: true,
            }))
            // options
            const targetOption = options.find(item => item.value === pCategoryId)
            targetOption.children = childOption
        }
        // 
        this.setState({ options })
    }
    componentDidMount() {
        this.getCategory('0')
    }
    componentWillMount() {
        const product = this.props.location.state
        this.isUpdate = !!product
        this.product = product || {}

    }
    render() {
        const { isUpdate, product } = this
        const { getFieldDecorator } = this.props.form
        const { categoryId, pCategoryId, imgs, detail } = product
        // 
        const categoryIds = []
        // id0 
        if (pCategoryId === '0') {
            categoryIds.push(categoryId)
            // 
        } else {
            categoryIds.push(pCategoryId)
            categoryIds.push(categoryId)
        }
        // layout 24
        const formItemLayout = {
            labelCol: { span: 2 },
            wrapperCol: { span: 8 },
        }
        // Card
        const title = (
            <span
                style={{
                    display: 'flex',
                    // justifyContent: 'center',
                    alignItems: 'center',
                    fontSize: '16px',
                    cursor: 'pointer',
                }}
                onClick={() => {
                    this.props.history.go(-1)
                }}
            >
                <Button type='link'
                    style={{ fontSize: 20 }}

                >
                    <Icon type="left-circle" />
                </Button>
                {isUpdate ? '' : ''}
            </span>
        )
        return (
            <Card title={title}>
                <Form {...formItemLayout}>
                    <Item label="">
                        {getFieldDecorator('name', {
                            initialValue: product.name,
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input></Input>
                        )}
                    </Item>
                    <Item label="">
                        {getFieldDecorator('desc', {
                            initialValue: product.desc,
                            rules: [{ required: true, message: '' }],
                        })(
                            <TextArea
                                autosize={{ minRows: 2, maxRows: 6 }}
                            />
                        )}
                    </Item>
                    <Item label="">
                        {getFieldDecorator('price', {
                            initialValue: product.price,
                            rules: [{ required: true, message: '' },
                            { validator: this.validatePrice }],
                        })(
                            <Input prefix="" addonAfter="RMB" type='number' />
                        )}
                    </Item>
                    <Item label="">
                        {getFieldDecorator('categoryIds', {
                            initialValue: categoryIds,
                            rules: [{ required: true, message: '' }],
                        })(
                            <Cascader
                                options={this.state.options}
                                loadData={this.loadData}
                                onChange={this.onChange}
                                changeOnSelect
                            />
                        )}
                    </Item>
                    <Item label="">
                        <PicturesWall ref={this.pw} imgs={imgs}></PicturesWall>
                    </Item>
                    <Item
                        label=""
                        labelCol={{ span: 2 }}
                        wrapperCol={{ span: 20 }}>
                        <RichTextEditor ref={this.editor} detail={detail}></RichTextEditor>
                    </Item>
                </Form>
                <Button
                    type='primary'
                    style={{ marginLeft: 35 }}
                    onClick={this.submit}
                ></Button>
            </Card>
        )
    }
}
export default Form.create()(ProductAddUpdate)

