import React, { Component } from 'react'
import {
  Card,
  Button,
  Table,
  Modal,
  message
} from 'antd'
import { formateDate } from "../../utils/dateUtils"
import { reqDeleteUser, reqUsers, reqAddOrUpdateUser } from "../../api"
import UserForm from './user-form'
import { PAGE_SIZE } from '../../utils/constants'

/*

 */
export default class User extends Component {

  state = {
    users: [], // 
    roles: [], // 
    isShow: false, // 
    loading: false
  }
  initColumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'username'
      },
      {
        title: '',
        dataIndex: 'email'
      },

      {
        title: '',
        dataIndex: 'phone'
      },
      {
        title: '',
        dataIndex: 'create_time',
        render: (create_time) => formateDate(create_time, true)
      },
      {
        title: '',
        dataIndex: 'role_id',
        render: (role_id) => this.roleNames[role_id]
      },
      {
        title: '',
        render: (user) => (
          <span>
            <Button type='link' onClick={() => this.showUpdate(user)}></Button>
            <Button type='link' onClick={() => this.deleteUser(user)}></Button>
          </span>
        )
      },
    ]
  }
  // role, (id)
  initRoleNames = (roles) => {
    const roleNames = roles.reduce((pre, role) => {
      pre[role._id] = role.name
      return pre
    }, {})
    // 
    this.roleNames = roleNames
  }
  // 
  showAdd = () => {
    this.user = null // user
    this.setState({ isShow: true })
  }
  // 
  showUpdate = (user) => {
    this.user = user // user
    this.setState({
      isShow: true
    })
  }
  // 
  deleteUser = (user) => {
    Modal.confirm({
      title: `${user.username}?`,
      onOk: async () => {
        const result = await reqDeleteUser(user._id)
        if (result.status === 0) {
          message.success('!')
          this.getUsers()
        }
      }
    })
  }
  // /
  addOrUpdateUser = async () => {
    this.setState({ isShow: false })
    // 1. 
    const user = this.form.getFieldsValue()
    this.form.resetFields()
    // , user_id
    if (this.user) {
      user._id = this.user._id
    }
    // 2. 
    const result = await reqAddOrUpdateUser(user)
    // 3. 
    if (result.status === 0) {
      message.success(`${this.user ? '' : ''}`)
      this.getUsers()
    }
  }
  getUsers = async () => {
    this.setState({ loading: true })
    const result = await reqUsers()
    this.setState({ loading: false })
    if (result.status === 0) {
      const { users, roles } = result.data
      this.initRoleNames(roles)
      this.setState({
        users,
        roles
      })
    }
  }
  componentWillMount() {
    this.initColumns()
  }
  componentDidMount() {
    this.getUsers()
  }
  render() {
    const { users, roles, isShow, loading } = this.state
    const user = this.user || {}
    return (
      <Card>
        <div style={{marginBottom: 14}}>
          <Button type='primary' onClick={this.showAdd}></Button>
        </div>
        <Table
          rowKey='_id'
          dataSource={users}
          columns={this.columns}
          pagination={{ defaultPageSize: PAGE_SIZE }}
          loading={loading}
        />
        <Modal
          title={user._id ? '' : ''}
          visible={isShow}
          onOk={this.addOrUpdateUser}
          onCancel={() => {
            this.form.resetFields()
            this.setState({ isShow: false })
          }}
        >
          <UserForm
            setForm={form => this.form = form}
            roles={roles}
            user={user}
          />
        </Modal>

      </Card>
    )
  }
}