import React, { Component } from 'react'
import { Card, Button, Icon, Table, message, Modal } from 'antd'
import { reqAddMenu, reqUpdateMenu, reqDeleteMenu } from '../../api'
import MenuForm from '../../components/menu-form'
import { PAGE_SIZE } from '../../utils/constants'
import SearchTree from './search-tree'
import SplitPane from 'react-split-pane'
export default class Menu extends Component {
  state = {
    tableData: [],
    addComponentCategorys: [],
    loading: false,
    isUpdate: false,
    confirmLoading: false,
    visible: 0, //0modal 1modal 2
    menu: {}, // menu
    _id: undefined,  // 
  }
  // 
  initColumns() {
    this.columns = [
      {
        title: '',
        dataIndex: 'title',
        key: 'title',
      },
      {
        title: 'URL',
        dataIndex: 'url',
        key: 'url',
      },
      {
        title: 'Icon',
        dataIndex: 'icon',
        key: 'icon',
      },
      {
        title: '',
        width: '15%',
        // dataIndex: 'address',
        key: 'address',
        render: (menu) => {
          return (
            <span>
              <Button type='link' onClick={this.showUpteModal.bind(null, menu)}></Button>
              <Button type='link' onClick={() => { this.deleteMenu(menu) }}></Button>
            </span>
          )
        }
      },
    ];
  }
  deleteMenu = async (record) => {
    let res = {}
    if (record._id) {
       res = await reqDeleteMenu(record._id)
    } else {
       res = await reqDeleteMenu(this.state._id, record.key)
    }
    if (res.status === 0) {
      message.success(res.msg)
      this.setState({ isUpdate: Math.random()*100 })
    } else {
      message.error(res.msg)
    }
  }
  // modal
  showUpteModal = (menu) => {
    this.setState({ visible: 1 })
    // this.menu = menu
    this.setState({ menu })
  }
  // modal
  showAddModal = () => {
    this.setState({ visible: 2 })
  }
  // 
  handleCancel = () => {
    // 
    this.form.resetFields()
    // 
    this.setState({ visible: 0, menu: {} })
  }
  // 
  addMenu = () => {
    // 
    this.form.validateFields(async (err, values) => {
      if (!err) {
        // 
        values.key = values.url
        // 
        this.setState({ confirmLoading: true })
        const result = await reqAddMenu(values, this.state._id)
        if (result.status === 0) {
          message.success('')
          this.setState({ confirmLoading: false })
          // 
          this.setState({ visible: 0, isUpdate: Math.random() * 100 })
          // 
          this.form.resetFields()
        } else {
          message.error('')
        }

      }
    })
  }
  // 
  updateMenu = () => {
    // 
    this.form.validateFields(async (err, values) => {
      if (!err) {
        // 
        values.key = values.url
        this.setState({ confirmLoading: true })
        const result = await reqUpdateMenu(values, this.state.menu._id, this.state._id)
        if (result.status === 0) {
          message.success('')
          this.setState({ confirmLoading: false })
          // 
          this.setState({ visible: 0, isUpdate: Math.random() * 100 })
          // 
          this.form.resetFields()
        } else {
          message.error('')
        }
      }
    })
  }
  getTableData = (tableData, loading, _id) => {
    if (!tableData) {
      this.setState({ loading: loading, _id })
    } else {
      this.setState({ tableData, loading: loading, _id })
    }
  }
  resetIsUpdate = () => {
    this.setState({ isUpdate: false })
  }
  componentDidMount() {
  }
  componentWillMount() {
    // 
    this.initColumns()
  }
  render() {
    let { loading, tableData, confirmLoading, isUpdate, menu } = this.state
    const category = this.category || {}
    return (
      <SplitPane split="vertical" minSize={230} maxSize={300} style={{ position: 'unset' }}>
        <SearchTree getTableData={this.getTableData} isUpdate={isUpdate ? this.resetIsUpdate : null} />
        <Card>
          <div style={{ marginBottom: 14 }}>
            <Button type="primary"
              onClick={this.showAddModal}
            >
              <Icon type='plus'></Icon>
            </Button>
          </div>
          <Table
            dataSource={tableData}
            columns={this.columns}
            loading={loading}
            rowKey='_id'
            pagination={{ defaultPageSize: PAGE_SIZE, showQuickJumper: true }}
          />
          <Modal
            title={this.state.visible === 1 ? "" : ""}
            visible={this.state.visible !== 0}
            onOk={this.state.visible === 1 ?  this.updateMenu : this.addMenu}
            confirmLoading={confirmLoading}
            onCancel={this.handleCancel}
          >
            <MenuForm categoryName={category.name} { ...menu }
              setForm={(form) => { this.form = form }}
            />
          </Modal>
        </Card>
      </SplitPane>
    )
  }
}
