import React, { Component } from 'react'
import { Form, Input } from 'antd';
import PropTypes from 'prop-types'
class MenuForm extends Component {
    static propType = {
        name: PropTypes.string,//
        url: PropTypes.string,
        icon: PropTypes.string,
        setForm: PropTypes.func.isRequired
    }
    // form
    componentWillMount() {
        this.props.setForm(this.props.form)
    }
    render() {
        const { getFieldDecorator } = this.props.form
        const { url, icon, title } = this.props
        return (
            <Form>
                <Form.Item
                label={''}
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 16 }}
                >
                    {getFieldDecorator('title', {
                        initialValue: title,
                        rules: [{ required: true, whitespace: true, message: '!' },]
                    })(<Input placeholder=''></Input>)}
                </Form.Item>
                <Form.Item
                label={'URL'}
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 16 }}
                >
                    {getFieldDecorator('url', {
                        initialValue: url,
                        rules: [{ required: true, whitespace: true, message: 'URL!' },]
                    })(<Input placeholder='URL'></Input>)}
                </Form.Item>
                <Form.Item
                label={'icon'}
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 16 }}
                >
                    {getFieldDecorator('icon', {
                        initialValue: icon,
                        rules: [{ required: true, whitespace: true, message: 'icon!' },]
                    })(<Input placeholder='icon'></Input>)}
                </Form.Item>
            </Form>
        )
    }
}

export default Form.create()(MenuForm)
